<template>
  <div id="app">
  	<h1>{{title}}</h1>
  	<input type="" name="" id="" value=""  v-model="newItem" @keyup.enter="en"/>
  	<ul>
  		<li v-for="item in items" :class="{isC:item.isComplete}" @click="complete(item)">{{item.label}}</li>
  	</ul>
    <!--<img src="./assets/logo.png">-->
    <!--<router-view/>-->
  </div>
</template>

<script>
	import Store from './Store'
export default {
  name: 'App',
  data (){
  			return{
  				 title:"标题",
  				 items:Store.fetch(),
  				 newItem:"",
  			}
  },
  methods:{
  	    complete:function(item){
  	    		item.isComplete = !item.isComplete;
  	    },
  	    en:function(){
  	      if(this.newItem != ''){
  	    		this.items.push({label:this.newItem,isComplete:false});
  	    	   this.newItem = '';
  	      }
  	    	   
  	    }
  },
  watch:{
  	   items:{
  	   	handler:function(items){
	   		Store.save(items);
  	   	},
  	   	deep:true,
  	   }
  }
}
</script>

<style>
	.isC{
		text-decoration: line-through;
	}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



